@import "./src/sass/helpers/var";

.contacts {
  padding: 7.5% 0 0;
  width: 100%;

  &--wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin: 0 auto; }

  &__title {
    display: flex;
    padding: 2.5% 15%;
    font-size: 2.5rem; } }

.map {
  &--wrapper {
    width: 100%;
    display: flex;

    iframe {
      width: 100%; } } }

