@import "./src/sass/helpers/var";

.chooseLanguage {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 7.5%;
  margin: 0 auto;

  &__item {
    display: flex;
    font-size: calc(0.25rem + 0.5vw);
    border: none;
    text-align: center;
    justify-content: center;
    align-items: center;
    padding: 0 0.5rem;
    transition: $transition;
    color: $blue;
    background-color: transparent;

    &:hover {
      cursor: pointer;
      color: $light-blue; }

    &:nth-child(2n) {
      border: {
        left: 1px solid $blue;
        right: 1px solid $blue; } } } }

