@import "../helpers/var";

.enterprise-header {
    width: 100%;
    display: flex;
    background: rgba(0,0,0,0.25) url('../img/1.jpg') center / cover no-repeat;

    &--wrapper {
        margin: 0 auto;
        width: 70%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center; }

    &__logo {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 15% auto 2.5%;

        img {
            height: 100%;
            width: 100%;
            object-fit: cover; } }

    &__title {
        padding: 5% 0;
        display: flex;
        color: $white;
        font-size: 2.5rem;
        font-weight: 500;
        flex-direction: column;
        align-items: center;
        line-height: 3.5rem;

        .bold {
            font-size: 3.75rem; } } }

.enterprise {
    display: flex;
    width: 100%;

    &--wrapper {
        width: 70%;
        margin: 0 auto;
        display: flex;
        padding: 7.5% 0;

        &:hover {
            & .enterprise-img--after {
                background-size: 105%; } } }

    &__item {
        display: flex;
        flex-direction: column; } }

.enterprise-img {
    position: relative;
    width: 35%;
    margin: 0 auto 0 0;

    &--before,
    &--after {
        // position: absolute
        width: 90%;
        height: 90%;
        display: inline-block;
        background: rgba(0,0,0,0.25) url('../img/5.png') center / 100% no-repeat; }

    &--before {
        position: relative;
        &::before {
            top: 0;
            left: 0;
            display: block;
            content: '';
            width: 100%;
            height: 100%;
            background-color: rgba(0,0,0,0.5); } }

    &--after {
        position: absolute;
        top: 10%;
        left: 10%;
        transition: all 0.3s ease-in-out; } }

.enterprise-description {
    width: 50%;

    &__title {
        font-size: 1.5rem;
        font-weight: 600;
        display: flex;
        width: 100%;
        color: $yellow; }

    &__legend {
        width: 100%;
        display: inline-block;
        padding: 2.5rem 0;
        font-size: 1rem;
        text-align: justify;
        line-height: 1.5rem; } }

.advantage-list {
    display: flex;
    width: 100%;
    flex-direction: column;
    padding: 3.5%;
    border: 1px solid $yellow;

    &__item {
        margin: 0.5rem 0;
        font-size: 1rem;
        display: flex;
        align-items: center; } }

.advantage {
    span {
        margin-right: 0.25vw; }
    &__icon {
        display: inline-block;
        width: 1.5rem;
        height: 1.5rem;
        background: url('../img/Project_about_icon1.png') center / cover no-repeat;

        &1 {
            background-image: url('../img/Project_about_icon1.png'); }
        &2 {
            background-image: url('../img/Project_about_icon2.png'); }
        &3 {
            background-image: url('../img/Project_about_icon3.png'); }
        &4 {
            background-image: url('../img/Project_about_icon4.png'); }
        &5 {
            background-image: url('../img/Project_about_icon5.png'); } } }
