@import "./src/sass/helpers/var";

.navigation {
  width: 60%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;

  &--wrapper {
    display: inherit;
    width: 100%;
    justify-content: space-between;
    align-items: center; }

  &__item {
    display: flex;
    width: 100%; }

  &__link {
    display: flex;
    padding: 3rem 0.75rem;
    width: 100%;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    font-size: calc(0.25rem + 0.65vw);
    transition: $transition;
    color: $blue;
    font-weight: 500;

    &:hover,
    &:focus,
    &:active {
      color: $light-blue; } } }

.menu {
  display: none;
  position: relative;
  width: 30px;
  height: 25px;
  margin: auto;

  &:hover {
    cursor: pointer; }

  span {
    margin: 0 auto;
    top: 12px;
    position: relative;
    width: 100%;
    height: 6px;
    background-color: $black;
    display: block;

    &:before,
    &:after {
      position: absolute;
      content: '';
      width: 100%;
      height: 6px;
      background-color: $black;
      display: block; }

    &:before {
      margin-top: -12px; }

    &:after {
      margin-top: 12px; } }

  &--toggle {
    margin: 2.5% auto;
    span {
      transition-duration: 0s;
      background-color: rgba($black, 0);
      transition-delay: 0.2s;

      &:before,
      &:after {
        transition-property: margin, transform;
        transition-duration: 0.2s;
        transition-delay: 0.2s, 0s;
        margin-top: 0; }

      &:before {
        transform: rotate(45deg); }

      &:after {
        transform: rotate(-45deg); } } } }

